<template>
  <div v-if="selected_article.id">
    <BreadCrumbs cl v-if="selected_article.id" v-bind="{ ...selected_article }" />
    <h2 class="border-bottom header-body pt-0 text-capitalize" v-if="selected_article">{{ selected_article.name }}</h2>
    <div v-if="selected_article" v-html="selected_article.content" class="pb-5"></div>
  </div>
</template>
<script>
  import BreadCrumbs from "./BreadCrumbs";
  import Articles from "@/js/support/cname/articles";
  import { bus } from "@/js/core/emit-bus";

  export default {
    name: "ArticlePreview",
    data() {
      return {
        selected_article: "",
        total_articles: "",
        loading: false,
      };
    },
    components: { BreadCrumbs },
    async mounted() {
      if (this.$route.params.a_id) await this.getArticles(this.$route.params.c_id);
    },

    methods: {
      // Get article data under given section & categorys
      async getArticles(category_id, section_id) {
        this.total_articles = await Articles.get_Articles(category_id, section_id);
        // Find selected article
        this.selected_article = this.total_articles.find((data) => data.id == this.$route.params.a_id);
        bus.$emit("close_overlay", false);
      },
    },
  };
</script>
