<template>
  <div>
    <b-overlay :show="show" :class="show ? 'support-page-loader' : ''">
      <b-container>
        <b-row>
          <b-col>
            <Element :template="template" />
          </b-col>
        </b-row>
      </b-container>
    </b-overlay>
  </div>
</template>
<script>
  import Element from "./element";
  import { bus } from "@/js/core/emit-bus";
  export default {
    components: { Element },
    props: ["template"],
    data() {
      return {
        show: true,
      };
    },
    mounted() {
      // Once data is loaded the close the overlay
      bus.$on("close_overlay", (value) => {
        this.show = value;
      });
    },
  };
</script>
<style>
  .support-page-loader {
    height: 100vh;
    bottom: 0px !important;
    top: 0 !important;
    position: fixed !important;
    opacity: 0.8;
    left: 0;
    right: 0;
    width: 100%;
  }
  /* .support-page-loader .b-overlay {
    z-index: 1041 !important;
  } */
</style>
