<template>
  <b-container>
    <b-row>
      <b-col sm="12" class="px-0" v-if="total_articles.length">
        <BreadCrumbs v-if="total_articles.length" v-bind="{ ...total_articles[0] }" />
        <h2 class="border-bottom header-body pt-0 text-capitalize" v-if="total_articles.length">{{ total_articles[0].category_name }}</h2>
        <Sections :category_id="Number($route.params.c_id)" class="mt-1" v-if="$route.params.c_id" />
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
  import BreadCrumbs from "./BreadCrumbs";
  import Articles from "@/js/support/cname/articles";
  import Sections from "./Sections";
  import { bus } from "@/js/core/emit-bus";

  export default {
    name: "Category",
    components: {
      BreadCrumbs,
      Sections,
    },
    data() {
      return {
        total_articles: "",
      };
    },
    props: {},

    async created() {
      await this.getArticles(this.$route.params.c_id);
    },
    methods: {
      // Get total articles
      async getArticles(category_id) {
        this.total_articles = await Articles.get_Articles(category_id);
        bus.$emit("close_overlay", false);
      },
    },
  };
</script>
